body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#me {
	height: 150px;
	width: 150px;
	float: left;
	position: relative;
	left: 50%;
	margin-left: -75px;
	margin-bottom: 15px;
}

a, a:hover, a:focus {
	text-decoration: none;
}
.board {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);

  border-collapse: collapse;
  border: 3px solid #ccc;
}

.square {
  color: #ccc;
  background: white;
  border: 2px solid white;
  font-family: Arial;
  font-size: 26px;
  font-weight: bold;
  height: 36px;
  line-height: 36px;
  width: 36px;
  text-align: center;
  vertical-align: middle;
}

span {
  position: relative;
  top: -1px;
}

#solve-group {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(400%);
          transform: translateX(-50%) translateY(400%);
  width: 342px;
}

#solve-button {
  width: 297px;
}

.dropup-item {
  width: 339px;
  text-align: center;
  color: black;
}


/* iPhone 5 */
@media only screen and (max-width: 320px) {
  .square {
    height: 33px;
    width: 33px;
    line-height: 33px;
  }

  #solve-group {
    width: 315px;
  }

  #solve-button {
    width: 272px;
  }

  .dropup-item {
    width: 312px;
  }
}

/* iPad and larger desktop screens */
@media only screen and (min-width: 600px) and (min-height: 660px) {
  .square {
    height: 56px;
    line-height: 56px;
    width: 56px;
  }
  
  #solve-group {
    -webkit-transform: translateX(-50%) translateY(600%);
            transform: translateX(-50%) translateY(600%);
    width: 523px;
  }

  #solve-button {
    width: 478px;
  }

  .dropup-item {
    width: 520px;
  }
}

.square:focus {
  outline: none;
  border: 2px solid #ccc;
}

td {
  border: 1px solid #ccc;
}

td:nth-child(3n) {
  border-right: 3px solid #ccc;
}

tr:nth-child(3n) td {
  border-bottom: 3px solid #ccc;
}

.child-button {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	background-color: white;
	font-size: 26px;
	font-weight: bold;
	font-family: Arial;
	color: #ccc;
	box-shadow: 0px 1px 3px black;
}
.preview {
	display: block;
	height: auto;
	max-width: 300px;
	float: left;
	position: relative;
	left: 50%;
	margin-left: -150px;
	margin-top: 20px;
}

span.caption {
	display: table;
	border-radius: 4px;
	background: rgba(0,0,0,0.5);
	cursor: pointer;
	height: 300px;
	left: 50%;
	opacity: 0;
	margin-left: -150px;
	position: absolute;
	top: 0;
	width: 300px;
	transition: all 300ms;
}

span.caption span {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
	color: white;
	font-size: 2.8em;
	font-style: bold;
	transition: all 300ms;
}

span.caption:hover {
	opacity: 1;
}

span.caption span:hover {
	-webkit-transform: scale(0.9);
	transform: scale(0.9);
}

html {
    height: 100%;
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body {
    position: relative;
    margin: 0;
    padding-bottom: 6rem;
    min-height: 100%;
}

.body {
    margin: 0 auto;
}

.tagline-upper {
    line-height: 0.1;
}

.tagline-lower {
    font-size: 1.2rem;
    font-style: italic;
}

#home-page-header {
    text-align: center;
    position: absolute;
    top: 45%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}

.page-header {
    padding-top: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    width: 80%;
}

.nav, .navbar, .navbar-toggle {
    background-color: #FFF;
    border: none;
    margin-bottom: 0;
}

.nav, .navbar-collapse {
    border: none;
}

.footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 1rem;
    text-align: center;
}

.social-icon {
    height: 30px;
    width: 30px;
    color: black;
}

